body {
    font-family: "Muli", sans-serif;
    line-height: 1.6;
    background-color: #ffffff;
    overflow-x: hidden;
    font-size: 16px;
    color: #333;
}

body:before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.2);
    opacity: 0;
    visibility: hidden;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
}

body.offcanvas-menu:before {
    opacity: 1;
    visibility: visible;
}

.text-black {
    color: #000000 !important;
}

a {
    color: #5cccc9;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
}

a:hover {
    color: #5cccc9;
    text-decoration: none;
}

 ::selection {
    background: #000000;
